import {connect} from "react-redux";
import React, {Component} from "react";
import {GenericPopup} from "eshop/components/OraCommonComponents"
import {setCashInvoiceLimit, setCartInvoiceValue} from "eshop/modules/simfree/actions/app";
import OnlineUtils from "eshop/utils/OnlineUtils";
import OraModal from "eshop/modules/core/components/ui/Modal";
import $ from "jquery";

class OraCashInvoiceLimitComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {showModal: false}
        this.open = this.open.bind(this)
        this.onClose = this.onClose.bind(this)
        this.openData = {}
    }

    static openPopup(data) {
        OraCashInvoiceLimitComponent.openPopupInternal(data)
    }

    open(data){
        this.openData = data
        this.setState({showModal: true})
    }

    componentWillMount(){
        OraCashInvoiceLimitComponent.openPopupInternal = this.open;

        if(this.props.isCheckRequired){
            if(this.props.configuration){
                this.props.setCashInvoiceLimit(this.props.configuration.maxInvoiceValue);
                this.props.setCartInvoiceValue(this.props.cartInvoiceValue);
            } else {
                console.error("OraCashInvoiceLimitComponent initialization error. missing configuration");
            }
        }
    }

    onClose() {
        this.setState({showModal: false})
    }

    genericPopupProps(){
        return {
            ...OnlineUtils.extractObject(this.props.descriptions, "label.popup."),
            content: this.props.configuration.addToCartWarningMessage,
            ...this.openData
        }
    }

    render() {
        return (
            <OraModal
                id = "max-invoice-value-limit-popup"
                size = "narrow"
                escapeClose = {true}
                showClose = {true}
                clickClose = {true}
                open = {this.state.showModal}
                onClose = {this.onClose}>
                    <GenericPopup id = "max-invoice-value-limit-in-popup" {...this.genericPopupProps()} />
            </OraModal>
        )
    }
}

const mapStateToProps = state => ({
});

const mapDispatchToProps = dispatch=> ({
    setCashInvoiceLimit: (limit) => dispatch(setCashInvoiceLimit(limit)),
    setCartInvoiceValue: (value) => dispatch(setCartInvoiceValue(value))
});

export default connect(mapStateToProps, mapDispatchToProps)(OraCashInvoiceLimitComponent)
